import React from "react";
import Header from "components/header/Header.js";

import { compose, applyMiddleware, createStore } from "redux";
import { persistStore, autoRehydrate } from "redux-persist";
import { Provider } from "react-redux";
import reducer from "data/reducers";
import { createLogger } from "redux-logger";
const logger = createLogger({
	collapsed: true
});
var store;

export default class Layout extends React.Component {
	render() {
		console.log("purge? ", this.props.purge);
		if (this.props.purge) {
			store = createStore(reducer, applyMiddleware(logger));
			persistStore(store).purge();
		} else {
			store = createStore(reducer, compose(applyMiddleware(logger), autoRehydrate()));
			persistStore(store);
		}
		return (
			<Provider store={store}>
				<div className="Layout" style={Object.assign({ minHeight: "100vh" }, this.props.style || {})}>
					<Header />
					{this.props.children || null}
				</div>
			</Provider>
		);
	}
}



